@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin-left: 10%;
  margin-right: 10%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #3C4062, #0D0A0A) !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  color: white;
}

body {
  background: #141414;
}

.project-cards-container {
  display: flex;
  flex-direction: row;
}

h1 {
  text-align: center;
  color: #A1ABFC !important;
  margin: 2%;
}

h2 {
  color: #A1ABFC !important;
  margin-bottom: 2%;
}

h1,
h2,
h3,
h4,
p,
a {
  color: white;
}

h1,
h2,
h3,
h4{
  font-weight: 600;
}

ul {
  list-style-type: none;
}


:link {
  text-decoration: none;
}

hr {
  border: none;
  height: 1px;
  background-color: rgba(161, 171, 252, 0.2);
  margin-left: -10%;
  margin-right: -10%;
  filter: blur(1px);
  margin-bottom: 2%;
}

p {
  font-size: 20px;
  margin-bottom: 1%;
}

.no-margin-p {
  margin-bottom: 0%;
}

section {
  margin-bottom: 2%;
}

.a-project-links {
  text-align: center;
  color: #6889FF;
  transition: color 0.3s ease, transform 0.3s ease;
}

.a-project-links:hover {
  color: #A1ABFC;
}

img{
  border-radius: 20px;;
}

.main-body{
  padding-top: 4rem;
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 5rem;
}